.header-color{
  background: #03001e;  /* fallback for old browsers */
  background: blue;  /* Chrome 10-25, Safari 5.1-6 */
  background: blue; /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  
  }
  
  .landing-grid{
  
  
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%; 
  text-align: center;
  }
  
  .contacts-body {
    margin: auto;
    background: grey;
    position: fixed;
    width: 100%;
    height: 100%;
    justify-content: center;
    padding-top: 2em;
    
  }
  
  .contacts-grid {
    text-align: center;
    justify-content: center;
    width: 80%;
    height: 550px;
    background: white;
    color: black;
    margin-bottom: 20px;
    border:2px solid red;
    background: white;  /* Chrome 10-25, Safari 5.1-6 */
 
  }
  
  
  .avatar-img{
    height: 250px;
  padding-top: 5em;
  position: center;
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 20%;
  
  }
  
  .banner-text {
    
    opacity: .8;
    width: 75%;
    margin: auto;
    border-radius: 10px;
    padding-top: 15%;
  }
  
  .banner-text h1 {
    font-size: 66px;
    font-weight: bold;
    color: rgb(19, 1, 1);
  }
  
  .banner-text hr {
    border-top: 5px dotted white;
    width: 50%;
    margin: auto;
  }
  
  .banner-text p {
    color: white;
    font-size: 20px;
    padding: 1em;
  }
  
  .social-links{
    display: flex;
    justify-content: space-between;
    width: 50%;
    margin: auto; 
    color: aqua; 
    height: 50px;
    width: 50%;
    font-size: 2em;
    padding: 8px;
    
  }
  
  .button-bootstrap{
    display: flex;
    justify-content: space-between;
    color: #7303c0;
  }
  
  .projects-grid{
    display: flex;
  }
  
  .contacts-grid h2{
    font-family: 'Merriweather', sans-serif ;
  }
  
  .contacts-grid p{
    font-family: 'Raleway', sans-serif ;
    font-weight: bold;
    width: 100%;
    font-size: 0.7em;
    scroll-behavior: auto;
  } 
  
  .contacts-list i{
    /* font-size: 66px; */
    padding-right: 2rem;
  }
  
  
  .text-container{
    margin: auto;
  }
  
  .contacts-paragraph{
    width: 100%;
    height: 90%;
    margin: auto;
    padding-top: '1em';
    flex: 1;
    text-align: justify;
    font-weight: bold;
    
    
  }
  
  .center {
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 20%;
    padding-top: 3em; 
    text-align: center;
  }
  
